import React, { useState } from 'react'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'
import { Alert } from 'reactstrap'

export const LogIn = () => {
    const [formData, setFormData] = useState({})
  const [visible, setVisible] = useState(false)
  const [visible1, setVisible1] = useState(false)

  const navigate = useNavigate()

  const handleInfo = (e) =>{
    setFormData({ ...formData, [e.target.name]:e.target.value })
  }

  const handleLogin = (e) => {
    e.preventDefault()
    console.log(formData)
    axios.post('http://localhost:4000/api/user/login', formData)
    .then(res => {
      console.log(res.data)
      if(res.data.success === true){
      setVisible(true)
      setTimeout(() => {
        setVisible(false)
        navigate('/')
      }, 2000)} else{
        setVisible1(true)
      setTimeout(() => {
        setVisible1(false)
      }, 2000)
      }
    })
    .catch(err => console.log(err.message))
  }
  return (
    <div className="container">

<Alert color="info" style={{position:'fixed', top:'0', width:'80%', margin:'auto'}} isOpen={visible}>
      Log In success
    </Alert>
<Alert color="danger" style={{position:'fixed', top:'0', width:'80%', margin:'auto'}} isOpen={visible1}>
      Please Provide valid Credentials
    </Alert>

    <form className='my-box m-auto' onSubmit={handleLogin}>
    <h1>Log In</h1>

<div className="form-group mt-2">
 <label htmlFor="email">Email</label>
 <input onChange={handleInfo} name='email' type="email" className="form-control" id="email" placeholder="Enter A Valid Email"/>
</div>
<div className="form-group mt-2">
 <label htmlFor="password">Password</label>
 <input onChange={handleInfo} name='password' type="text" className="form-control" id="password" placeholder="Enter A Strong Password"/>
</div>

<button type="submit" className="btn btn-primary mt-2">Submit</button>
</form>

</div> 
  )
}
